@layer base {
  body {
    @apply text-blue font-montserrat;
    @apply bg-linen;
  }
}

@layer components {
  p {
    @apply text-justify;
  }

  .theme-dark {
    @apply inline-block w-full bg-blue text-white;
  }

  .dotted-list {
    li {
      @apply pl-4 relative;
      @apply before:w-2 before:h-2 before:rounded-full before:inline-block before:absolute before:left-0;
      @apply before:top-0 before:bottom-0 before:m-auto;
    }

  }

  .dotted-list-top {
    li {
      @apply before:bottom-[calc(100%_-_1.5em)] #{!important};
    }
  }

  .dotted-list-empty {
    @apply dotted-list;
    li {
      @apply before:border-gold before:border;
    }
  }

  .dotted-list-filled {
    @apply dotted-list;
    li {
      @apply before:bg-gold;
    }
  }

  .def-text {
    @apply text-[16px] md:text-[12px] leading-snug;
    h1 {
      @apply text-5xl sm:text-3xl font-bold font-roman mb-2;
    }

    h2 {
      @apply text-4xl sm:text-2xl font-bold font-roman mb-2;
    }

    h3, h4, h5, h6 {
      @apply text-3xl sm:text-xl font-bold font-roman mb-2;
    }

    p {
      @apply my-5;
    }

    ul {
      @apply my-5;
      li {
        @apply pl-4 relative mb-3;
        @apply before:w-2 before:h-2 before:rounded-full before:border-gold before:inline-block before:border before:absolute before:left-0;
        @apply before:top-0 before:bottom-0 before:m-auto;
      }
    }

    ol {
      @apply my-5;
      @apply list-decimal;
      li {
        @apply ml-4;
      }
    }

  }

  .hover-pulse {
    @apply hover:drop-shadow ease-in-out duration-300 hover:scale-[0.95] transition;
  }

  .scrollbar {
    &::-webkit-scrollbar {
      width: 8px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
      background-color: #fff;
    }


    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
      cursor: pointer;

      background-color: #a08571;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #D3B096;
      background-clip: padding-box;
      border: 2px solid #fff;
      border-radius: 3px;
    }
  }

  .breadcrumbs {
    & > div {
      & > span {
        @apply flex gap-2 py-[60px] md:pt-[30px] md:pb-[30px] md:text-[12px] font-medium;
        & > span {
          @apply after:w-1 after:h-1 after:rounded-full after:bg-gold after:inline-block flex items-center gap-2 last:after:hidden;
          @apply last:text-gold;
          a {
            @apply hover:text-gold transition
          }
        }
      }
    }
  }

  .wpcf7 {
    .container {
      max-width: 100%;
      padding: 0;
    }
    &.contact-form{
      .row{
        @apply md:flex-col;
      }
    }

    .row {
      @apply flex;
      .full {
        @apply basis-full;
      }

      .one-half {
        @apply basis-1/2;
      }
    }

    .field {
      @apply p-1 relative has-[.wpcf7-not-valid-tip]:mb-6;
      label {
        @apply absolute text-red-500 left-2 top-0 bottom-0 flex justify-center items-center z-10 #{!important};
      }

      &:has([aria-invalid=true]) {
        .wpcf7-not-valid-tip {
          @apply block;
        }
      }
    }

    .wpcf7-not-valid-tip {
      @apply hidden absolute w-full top-full left-0 text-left text-sm;


    }

    .form-control {
      @apply form-input;
      @apply w-full rounded-full #{!important};
      // important to override plugin styles
    }

    a {
      @apply underline text-gold;
    }

    .btn {
      @apply w-full mb-1;
      @apply rounded-full #{!important};
    }
  }

  // .scrollbar-x-xs{
