  h1.title {
    @apply text-gold font-roman text-[42px] md:text-[20px] uppercase;
  }

  h2.title {
    @apply text-gold font-roman text-4xl md:text-lg uppercase;
  }

  .card {
    @apply bg-white rounded-2xl overflow-hidden;
    @apply flex flex-col;
    figure {
      @apply flex items-center justify-center;
      @apply min-w-0 min-h-0;
      img {
        @apply w-full h-full object-cover object-center;
      }
    }
  }

  .card-body {
    @apply p-8 md:p-5;
  }

  .card-wide {
    .card-body {
      @apply p-12 md:p-5;
    }
  }

  .card-compact {
    .card-body {
      @apply p-4 md:p-4;
    }
  }

  .card-compact-xs {
    .card-body {
      @apply p-2 md:p-2;
    }
  }

  .card-side {
    @apply flex-row;
  }


  .btn {
    @apply p-4 rounded-full border-[1.45px] border-gold text-center font-roman text-xl font-bold;
    @apply md:p-2 md:text-lg;
    @apply hover:[&:not(:disabled)]:bg-gold hover:[&:not(:disabled)]:text-white transition cursor-pointer;
    @apply active:shadow-inner-md;
    @apply disabled:opacity-80 disabled:bg-gray-200 disabled:border-gray-400;
  }

  .dropdown-radio-menu {
    ul {
      @apply max-h-[300px] overflow-y-auto;
    }

    ul .ui-menu-item div {
      @apply relative;
      padding-left: 40px !important;

      &:before {
        content: '';
        position: absolute;
        left: 10px;
        top: 0;
        bottom: 0;
        margin: auto;
        width: 22px;
        height: 22px;
        @apply border border-gold rounded-full bg-white;
      }

      &:after {
        content: '';
        @apply z-10 absolute;

        left: calc(10px + (22px - 6px) / 2);
        top: 0;
        bottom: 0;
        margin: auto;
        @apply bg-gold transition h-1.5 w-1.5 rounded-full scale-0;
      }

      &.ui-state-active:after {
        @apply scale-100;
      }
    }

    ul:before {
      content: '';
      width: 1px;
      position: absolute;
      height: 100%;
      background: #D3B096;
      padding: 25px 0;
      left: calc(20px);
      top: 0;
      bottom: 0;
      z-index: 0;
      background-clip: content-box;
    }
  }

  .dropdown-menu-gold {
    ul {
      @apply bg-white rounded-lg shadow-lg border border-gold;
      @apply scrollbar;
    }

    & .ui-menu-item.ui-state-disabled .ui-menu-item-wrapper {
      @apply text-neutral-400;
    }

    & .ui-menu-item:not(.ui-state-disabled) .ui-menu-item-wrapper:hover, .dropdown-menu .ui-menu-item:not(.ui-state-disabled) .ui-state-active {
      @apply text-gold;
    }
  }

  .dropdown-menu {
    @apply opacity-0 pointer-events-none transition z-20 top-0;
    ul {
      @apply -translate-y-5 transition  relative;
      .ui-menu-item div {
        @apply p-3 relative overflow-hidden cursor-pointer transition;
      }
    }

    &.ui-selectmenu-open {
      @apply w-auto pt-2 pointer-events-auto opacity-100 translate-y-0;
      ul {
        @apply translate-y-0;
      }
    }
  }

  .form-input {
    @apply bg-white rounded-full border-2 border-neutral-200 text-stone-500 placeholder:opacity-90 p-5 md:py-[10px] md:text-[12px];
    @apply focus:outline-transparent focus:ring-2 ring-gray-300 ring-offset-2 transition;
    @apply invalid:outline-red-500 invalid:outline-1 invalid:outline;
    @apply group-has-[:invalid]/input:outline-red-500 group-has-[:invalid]/input:outline-1 group-has-[:invalid]/input:outline ;
    @apply group-[&.hide-errors]/form:outline-none;
  }

  .status-dot {
    @apply w-[5px] h-[5px] rounded-full bg-current relative before:absolute before:inset-0 before:rounded-full before:border before:border-current;
    &.ping-once {
      @apply before:animate-ping-lg-once;
    }

    &.ping {
      @apply before:animate-ping-lg;
    }

  }

}

