/* HTML: <div class="loader"></div> */
.loader {
  width: 50px;
  aspect-ratio: 1;
  color: #000;
  border: 2px solid;
  display: grid;
  box-sizing: border-box;
  animation: l1 4s infinite linear;
}

.loader::before,
.loader::after {
  content: "";
  grid-area: 1/1;
  margin: auto;
  width: 70.7%;
  aspect-ratio: 1;
  border: 2px solid;
  box-sizing: content-box;
  animation: inherit;
}

.loader::after {
  width: 50%;
  aspect-ratio: 1;
  border: 2px solid;
  animation-duration: 2s;
}

@keyframes l1 {
  100% {
    transform: rotate(1turn)
  }
}

.product-nav-slider {
  .slick-track {
    margin-left: 0;
    margin-right: 0;
  }
}

.text-adaptive {
  font-size: calc((100vw - 600px) / (1600 - 600) * (var(--text-size-no-px) - var(--text-mobile-size-no-px)) + var(--text-mobile-size));
  @media screen and (min-width: 1600px) {
    font-size: var(--text-size);
  }
  @media screen and (max-width: 600px) {
    font-size: var(--text-size-mobile);
  }
}
